*{
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
}

:root{
  --light-background: #eee;
  --dark-background: #FFF;
  --black-color: #000;
  --white-color: #FFF;
  --primary: #a31545;
  --secondary: #e91e63;
  --light: #ff4081;
}

body{
  background: var(--light-background);
}